import { RectRange } from '../../table/tablebase/RectRange';
import { RowsIterator } from '../../table/iterator/RowsIterator';
import { ColsIterator } from '../../table/iterator/ColsIterator';

class CopyFormatHelper {

  /**
   * 
   * @param originView
   * @param targetView
   * @param callback
   */
  static each({
    originView,
    targetView,
    callback = () => {},
  }) {
    const {
      sri: tSri, sci: tSci,
    } = targetView;
    const {
      sri: oSri, sci: oSci, eri: oEri, eci: oEci,
    } = originView;
    let oi = oSri;
    let oj = oSci;
    let ti = tSri;
    let tj = tSci;
    RowsIterator.getInstance()
      .setBegin(oSri)
      .setEnd(oEri)
      .setLoop((i) => {
        oi = i;
        ColsIterator.getInstance()
          .setBegin(oSci)
          .setEnd(oEci)
          .setLoop((j) => {
            oj = j;
            callback(ti, tj, oi, oj);
          })
          .setNext((j) => {
            tj = j;
          })
          .execute();
      })
      .setNext((i) => {
        ti = i;
      })
      .execute();
  }

  /**
   * 
   * @param originView
   * @param targetView
   * @param mergeCheck
   * @param masterCheck
   * @param cellOrMaster
   * @param mergeExecute
   * @param copyExecute
   * @param mergeRemove
   */
  static copy({
    originView,
    targetView,
    mergeCheck = () => {},
    masterCheck = () => {},
    cellOrMaster = () => {},
    mergeExecute = () => {},
    copyExecute = () => {},
    mergeRemove = () => {},
  }) {
    this.each({
      originView,
      targetView,
      callback: (tri, tci, ori, oci) => {
        // 
        if (masterCheck(ori, oci)) {
          // 
          const origin = cellOrMaster(ori, oci);
          const target = cellOrMaster(tri, tci);
          copyExecute(origin, target, tri, tci);
          // 
          const om = mergeCheck(ori, oci);
          const tm = mergeCheck(tri, tci);
          if (tm) {
            mergeRemove(tm);
          }
          if (om) {
            const [rn, cn] = om.size();
            mergeExecute(new RectRange(tri, tci, tri + (rn - 1), tci + (cn - 1)));
          }
        }
      },
    });
  }

}

export {
  CopyFormatHelper,
};
